.form__fieldset{
    border:none;
}

.form__currencySelect{
    font-family:  "Lato";
    font-size: 15px;
    padding: 1%;
}

.form__paragraph{
    padding: 0,5%;
    width: 100%;
}

.form__input{
    padding: 2%;
    width: 90%; 
    border-radius: 4px;
    font-family: "Lato";
}

.form__button{
    width:40%;
    border: none;
    background-color: #F9247E;
    color: #ffffff;
    font-family: "Lato";
    font-size: 18px;
    padding: 2%;
    border-radius: 8px;
    margin-left: 0.5%;
    margin-right: 0.5%;
}

.form__button:hover {
    background-color: #be165c;
}

.form__result{
    font-size: 30px;
}

@media (max-width: 767px) {
    .calculatorField {
        margin-left: auto;
        margin-right: auto;
    }

    .form__paragraph {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
    }

    .form__button {
        margin: 5px;
    }
}
